* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  display: flex;
  max-width: 1300px;
  background-size: contain;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.signnin {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1300px;
  margin-left: 40px;
  width: 100%;
  height: 100vh;
}

.signnin .exit {
  padding: 0;
  margin: 0;
  margin-left: 50%;
  justify-content: space-around;
  align-items: flex-end;
  position: relative;
  width: 60px;
  height: 60px;
}

.signnin .exit img {
  width: 60px;
  height: 60px;
}

.signnin .sign_in_container {
  padding-right: 50px;
  border-radius: 5%;
  position: relative;
  margin-left: 350px;
  margin-right: 30px;
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, rgb(2, 0, 36) 0%, rgba(22, 9, 121, 0.7287289916) 56%, rgb(0, 212, 255) 100%);
  width: 400px;
  height: 400px;
  font-size: 15px;
}

.signnin .sign_in_container .container {
  display: inline-block;
  text-align: center;
  flex-wrap: wrap;
}

.signnin .sign_in_container .container input {
  font-size: 25px;
  width: 360px;
  height: 60px;
}

.signnin .sign_in_container .container #username {
  margin-top: 0px;
}

.signnin .sign_in_container .container #password {
  margin-top: 20px;
}

.signnin .sign_in_container .container button {
  width: 360px;
  height: 50px;
  margin-top: 20px;
  border-radius: 3px;
  border-color: blue;
  background-color: white;
  font-size: 20px;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
  letter-spacing: 1px;
}

.signnin .sign_in_container .container button::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  border-radius: 0px;
  background: rgba(0, 4, 255, 0.89);
  transition: height 0.3s;
  z-index: -1;
}

.signnin .sign_in_container .container button:hover {
  color: rgb(255, 255, 255);
  font-size: 25px;
}

.signnin .sign_in_container .container button:hover::after {
  height: 100%;
}

.signnin .sign_in_container .container a {
  display: flex;
  background-color: white;
  border-radius: 15px;
  justify-content: center;
  width: 250px;
  height: 50px;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
  letter-spacing: 1px;
}

.signnin .sign_in_container .container a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  border-radius: 15px;
  background: cyan;
  transition: height 0.3s;
  z-index: -1;
}

.signnin .sign_in_container .container a:hover {
  color: rgb(19, 18, 18);
}

.signnin .sign_in_container .container a:hover::after {
  height: 100%;
}

.signnin .sign_in_container .container #forgotten {
  height: 30px;
  margin-left: 60px;
  margin-top: 10px;
  padding-top: 5px;
  text-decoration: none;
}

.signnin .sign_in_container .container #register {
  margin-left: 10px;
  width: 380px;
  color: #000;
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  user-select: none;
  text-decoration: none;
  margin-top: 10px;
  padding-top: 12px;
}

